.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 57px 0;

  .logo {
    img {
      height: auto;
      width: 211px;

      @media screen and (max-width: 500px) {
        width: 150px;
        height: auto; 
      }
    }
  }

  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 57px;
  }

  a {
    text-decoration: none;
    color: #ff6060;
    padding: 10px;
    font-size: 22px;
    position: relative;

    li {
      transition: 0.2s;
      cursor: pointer;

      &:hover {
        color: #ff6060;
      }
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 0%;
      height: 2px;
      background: #ff6060;
      transition: all 0.3s;
    }

    &:hover::after {
      left: 0;
      width: 100%;
    }
  }

  .nav-active {
    position: relative;

    &::after {
      content: "";
      height: 2px;
      width: 0;
      background: #ff6060;
      position: absolute;
      border-radius: 10px;
      bottom: 5px;
      left: 50%;
      transform: translateX(-50%);
      -webkit-animation: anim 0.3s ease forwards;
      animation: anim 0.6s ease forwards;

      @-webkit-keyframes anim {
        to {
          width: 100%;
        }
      }
      @keyframes anim {
        to {
          width: 100%;
        }
      }
    }
  }

  @media screen and (max-width: 500px) {
    padding: 20px 0;
    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
    }

    a {
      font-size: 15px;
      padding: 5px;
    }
  }
}
